
import { mapGetters, mapActions } from 'vuex'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
  components: {
    Cell,
    Group
  },
  data () {
    return {
    }
  },
  computed: {
    ...mapGetters({
      Station: 'QueryStationChk',
      Persons: 'QueryUnchkedList'
    }),
    loadMore () {
      return this.$store.state.comm.loadMore 
    }
  },
  methods: {
    ...mapActions([
      'QueryUnchkedList'
    ]),
    call (phone) {
      plus.device.dial(phone, false)
    }
  },
  created () {
    const params = {
      shiftId: this.Station.shiftId,
      stationId: this.Station.stationId
    }
    const $this = this
    this.$store.state.comm.loadMore = 'loading'
    this.QueryUnchkedList(params).then((res) => {
      if($this.Persons.length ===0 ) {
        $this.$store.state.comm.loadMore = 'notdata'
      }else {
        $this.$store.state.comm.loadMore = 'success'
      }
    })
  }
}



